/* import provinceData from './city-data/province.js';
import cityData from './city-data/city.js';
import areaData from './city-data/area.js'; */
export default {
  data() {
    return {
      pickerValue: [0, 0, 0],
      provinceDataList: [],
      cityDataList: [],
      areaDataList: [],
			/* 是否显示控件 */
			showPicker: false,
			provinceData:[],
      cityData: [],
      areaData: [],
    };
  },
  created() {
    this.init()
  },
  props: {
    /* 默认值 */
    pickerValueDefault: {
      type: Array,
      default(){
				return [0, 0, 0]
			}
    },
    /* 主题色 */
    themeColor: String
  },
	watch:{
		pickerValueDefault(){
			this.init();
		}
	},
  methods: {
		init() {
	
			/* this.provinceDataList = provinceData;
			this.cityDataList = cityData[this.pickerValueDefault[0]];
			this.areaDataList = areaData[this.pickerValueDefault[0]][this.pickerValueDefault[1]]; */
			this.$Request.post('area/getallarea').then(res => {
				if (res.code == 200) {
					console.log(res);
					
					this.provinceData = res.datas.province;
					this.cityData = res.datas.city;
					this.areaData = res.datas.area;
					 this.provinceDataList = this.provinceData;
					this.cityDataList = this.cityData[this.pickerValueDefault[0]];
					this.areaDataList = this.areaData[this.pickerValueDefault[0]][this.pickerValueDefault[1]];
				}
			});
			//this.handPickValueDefault(); // 对 pickerValueDefault 做兼容处理
			this.pickerValue = this.pickerValueDefault;
		},
    show() {
      setTimeout(() => {
        this.showPicker = true;
      }, 0);
    },
    maskClick() {
      this.pickerCancel();
    },
    pickerCancel() {
      this.showPicker = false;
      this._$emit('onCancel');
    },
    pickerConfirm(e) {
      this.showPicker = false;
      this._$emit('onConfirm');
    },
    showPickerView() {
      this.showPicker = true;
    },
    handPickValueDefault() {
      if (this.pickerValueDefault !== [0, 0, 0]) {
        if (this.pickerValueDefault[0] > this.provinceData.length - 1) {
          this.pickerValueDefault[0] = this.provinceData.length - 1;
        }
        if (this.pickerValueDefault[1] > this.cityData[this.pickerValueDefault[0]].length - 1) {
          this.pickerValueDefault[1] = this.cityData[this.pickerValueDefault[0]].length - 1;
        }
        if (this.pickerValueDefault[2] > this.areaData[this.pickerValueDefault[0]][this.pickerValueDefault[1]].length - 1) {
          this.pickerValueDefault[2] = this.areaData[this.pickerValueDefault[0]][this.pickerValueDefault[1]].length - 1;
        }
      }
    },
    pickerChange(e) {
      let changePickerValue = e.mp.detail.value;
      if (this.pickerValue[0] !== changePickerValue[0]) {
        // 第一级发生滚动
        this.cityDataList = this.cityData[changePickerValue[0]];
        this.areaDataList = this.areaData[changePickerValue[0]][0];
        changePickerValue[1] = 0;
        changePickerValue[2] = 0;
      } else if (this.pickerValue[1] !== changePickerValue[1]) {
        // 第二级滚动
        this.areaDataList =
          this.areaData[changePickerValue[0]][changePickerValue[1]];
        changePickerValue[2] = 0;
      }
      this.pickerValue = changePickerValue;
      this._$emit('onChange');
    },
    _$emit(emitName) {
      let pickObj = {
        name: this._getLabel(),
        value: this.pickerValue,
				provinceCode:this.provinceDataList[this.pickerValue[0]].value,
        cityCode: this.cityDataList[this.pickerValue[1]].value,
				areaCode:this._getCityCode()
      };
      this.$emit(emitName, pickObj);
    },
    _getLabel() {
      let pcikerLabel =
        this.provinceDataList[this.pickerValue[0]].name +
        '-' +
        this.cityDataList[this.pickerValue[1]].name +
        '-' +
        this.areaDataList[this.pickerValue[2]].name;
      return pcikerLabel;
    },
    _getCityCode() {
      return this.areaDataList[this.pickerValue[2]].value;
    }
  }
};
